import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './nav.css';

export default function Nav() {
    const [isVisible, setIsVisible] = useState([
        { men: false },
        { women: false },
        { others: false },
    ]);
    const [under900px, setUnder900px] = useState(false);

    const visible = (category) => {
        return () => {
            setIsVisible(isVisible.map((item) => {
                for (let key in item) {
                    if (key === category) {
                        return { ...item, [key]: true };
                    } else {
                        return { ...item, [key]: false };
                    }
                }
                return item; // Add this line to satisfy array-callback-return
            }));
        };
    };

    useEffect(() => {
        const handleResize = () => {
            setUnder900px(window.innerWidth < 900);
        };

        handleResize(); // Call it initially
        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    return (
        <div className="nav-container">
            <nav>
                <ul className="nav justify-content-center">
                    <li className="nav-item">
                        <Link
                            to="/men/*"
                            onMouseEnter={visible('men')}
                            className="nav-link active"
                            aria-current="page"
                        >
                            Men
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/women/*" onMouseEnter={visible('women')} className="nav-link">
                            Women
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to="/others/*" onMouseEnter={visible('others')} className="nav-link">
                            others
                        </Link>
                    </li>
                </ul>
                {under900px && (
                    <>
                        {isVisible[0].men ? <MenCategory /> : null}
                        {isVisible[1].women ? <WomenCategory /> : null}
                        {/* {isVisible[2].others ? <OthersCategory /> : null} */}
                    </>
                )}
            </nav>
        </div>
    );
}


function MenCategory() {
    return (
        <div id="men">
            <Link to="/men/tops">Tops</Link>
            <Link to="/men/bottoms">Bottoms</Link>
            <Link to="/men/shoes">Shoes</Link>
        </div>
    );
}

function WomenCategory() {
    return (
        <div id="women">
            <Link to="women/tops">Tops</Link>
            <Link to="women/bottoms">Bottoms</Link>
            <Link to="women/shoes">Shoes</Link>
        </div>
    );
}

