import React from 'react';
import { useDispatch } from 'react-redux'; // Removed unused import
import { removeFromCart } from '../../store/loginSlice';
import './cart.css';

const CartItem = (props) => { // Corrected to define the functional component
  const { _id, name, price, image, size } = props.item;
  const dispatch = useDispatch();
  const removeItem = async () => {
    const userId = props.userId;
    const itemId = _id;
    dispatch(removeFromCart({ userId, itemId }));
    console.log("cartItem", props.item);

    try {
      const response = await fetch('/product/removeFromCart', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ userId, itemId }),
      });

      if (response.ok) {
        console.log('Item removed successfully!');
      } else {
        console.error('Failed to remove item from the cart:', response.statusText);
      }
    } catch (error) {
      console.error('Error removing item from the cart:', error);
    }
  };

  return (
    <div className="cart-item">
      <img src={image[0]} style={{ width: "80px", height: "100px" }} alt={name} />
      <div>
        <h5>{name}</h5>
        <p>size:  {size}</p>
        <p>$ {price}</p>
      </div>
      <button type="button" className="btn btn-warning" onClick={removeItem}>remove</button> {/* Changed class to className */}
    </div>
  );
};

export default CartItem; // Exporting the functional component correctly
