import React from 'react';
import { useNavigate } from 'react-router-dom';
import './detail.css';
import { useDispatch, useSelector  } from 'react-redux';
import { addCart } from '../../store/loginSlice';
import { useParams } from 'react-router-dom';
import { Image } from 'antd';
import { Button, Result } from 'antd';
import { notification } from 'antd';



export default function Detail() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();
  const items = useSelector((state) => state.items.items);
  const item = items.find((item) => item._id === id);
  const { name, image, price, description } = item || {name:"", image:[], price:"", description:""};
  console.log("detail",items);
  const userInfo = useSelector((state) => state.login.userInfo);


  const [api, contextHolder] = notification.useNotification();
  const openNotification = (placement) => {
    api.info({
      message: `Notification ${placement}`,
      description:
        'Please, LogIn to add items to the cart ',
      placement,
    });
  };


  const goBack = () => {
    navigate(-1);
  };
  const backHome = () => {
    navigate('/');
  };

  const addToCart = async () => {
    if (userInfo && userInfo._id) {
        // User is logged in, proceed with adding the item to the cart
        const userId = userInfo._id;
        const itemId = id;
        dispatch(addCart({ userId, itemId }));
        console.log("Add to cart:", userId, itemId);
        try {
            const response = await fetch('http://localhost:5500/product/addToCart', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ userId, itemId }),
            });

            if (response.ok) {
                // Handle success - you can navigate to a success page or show a success message
                console.log('Item added to cart successfully!');
                // // Example: Navigate to the cart page after successfully adding the item
                // navigate('/cart');
            } else {
                throw new Error('Failed to add item to cart');
            }
        } catch (error) {
            console.error('Error adding item to cart:', error);
            // Handle error - you can show an error message to the user
        }
    } else {
        // User is not logged in, prompt them to log in
        console.log('User not logged in. Please log in to add items to the cart.');
        openNotification('bottomRight')

    }
};


  return (
    <div >
      <div className='detailContainer'>
      {item?
        <div className='detailContainer'>
        <div id="carouselExampleControls" style={{ width: '30%' }} className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            {image&&image.length>0? image.map((img, index) => (
              <div key={index} className={`carousel-item ${index === 0 ? 'active' : ''}`}>
                <Image className=" d-block w-100" src={img} alt={`Slide ${index + 1}`} />
              </div>
            )):null}
          </div>
          <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="sr-only">Previous</span>
          </a>
          <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="sr-only">Next</span>
          </a>
        </div>
        <div className='description'>
          <h3>{name}</h3>
          <br/>
          <h6>{description}</h6>
          <br/>
          <h5 style={{marginBottom:"40px"}}> &nbsp;&nbsp;&nbsp;$ {price} CAD</h5>
          <div className='buttons'>
          {contextHolder}
          <button type="button" onClick={addToCart} className="btn btn-info btn-lg">Add to Cart</button>
            <button type="button" className="btn btn-outline-warning btn-lg" onClick={goBack}>
              Go Back
            </button> 
          </div>
        </div>
        </div>:  <Result
            status="404"
            title="404"
            subTitle="Sorry, the page you visited does not exist."
            extra={<Button onClick={backHome} type="primary">Back Home</Button>}
          />}
      </div>
    </div>
  );
}
