import React, { useEffect } from 'react';
import Header from './Components/header/Header';
import Main from './pages/main/Main';
import Footer from './Components/footer/Footer';
import Nav from './Components/nav/Nav';
import Detail from './pages/itemDetail/Detail';
import Home from './pages/home/Home';
import CheckOut from './pages/checkOut/CheckOut';
import SignIn from './pages/signIn/SignIn';
import SignUp from './pages/signUp/SignUp';
import { fetchItems } from './store/itemSlice';
import { fetchLoginStatus } from './store/loginSlice';
import { useDispatch, useSelector } from 'react-redux';
import itemSlice from './store/itemSlice';
import loginSlice from './store/loginSlice';
import thunk from 'redux-thunk';
import { Route, Routes, Navigate } from 'react-router-dom';
import { Provider } from 'react-redux';
import { configureStore } from '@reduxjs/toolkit';

const store = configureStore({
  reducer: {
    items: itemSlice,
    login: loginSlice,
  }, middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(thunk),
});

function MainApp() {
  const dispatch = useDispatch();
  
  useEffect(() => {
    dispatch(fetchItems());
    dispatch(fetchLoginStatus());
  }, [dispatch]);

  const items = useSelector((state) => state.items.items);
  const { isLogin, userInfo , error } = useSelector((state) => state.login);

  console.log('App user', userInfo, ' islogin ', isLogin, ' error ', error);
  console.log('App items', items);

  return (
    <div>
      <Header />
      <Nav />
      <Routes>
        <Route path="/:type/:position" element={<Main />} />
        <Route path="/signin" element={<SignIn />} />
        <Route path="/signup" element={<SignUp />} />
        <Route path="/:id" element={<Detail />} />
        <Route path="/checkout" element={<CheckOut />} />
        <Route path="*" element={<Navigate to="/" />} />
        <Route path="/" element={<Home />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default function App() {
  return (
    <Provider store={store}>
      <MainApp />
    </Provider>
  );
}
