import React,{useState} from 'react';
import { FloatButton } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { LogoutOutlined } from '@ant-design/icons';
import CartItem from '../../pages/cart/Cart';
import './header.css';
import { Link } from 'react-router-dom';
import { Drawer } from 'antd';
import {useSelector} from 'react-redux';
import { ShoppingCartOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';


export default function Header() {
  const navigate = useNavigate();
  const userInfo = useSelector((state) => state.login.userInfo);
  const items = useSelector((state) => state.items.items);
  const islogin = useSelector((state) => state.login.isLogin);
  const numberOfItems = userInfo ? userInfo.cart.length : 0;
  const [showButtons, setShowButtons] = useState(false);
  const [isEnter, setIsEnter] = useState(null);
  const [open, setOpen] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const userId = userInfo ? userInfo._id : null;
  const cart = userInfo ? userInfo.cart : [];
  const cartItems = items.filter((item) => cart.includes(item._id));
  const showDrawer = () => {
    setOpen(true);
    setIsClicked(!isClicked);
  };

  const onClose = () => {
    setOpen(false);
  };
  const checkout = () => {
    if (userInfo) {
      navigate('/checkout');
      setOpen(false);
    } else {
      navigate('/signin');
    }
  };
  const handleButtonClick = () => {
    setShowButtons(!showButtons);
  };
  const mouseEnter = (button) => {
    setIsEnter(button);
  };  


  const logOut = () => {
    console.log("click Logout");
    const xhr = new XMLHttpRequest();
    xhr.open('POST', 'http://localhost:5500/member/signout');
    xhr.send();

    xhr.onreadystatechange = () => {
      if (xhr.readyState === 4) {
        if (xhr.status >= 200 && xhr.status < 300) {
          window.location.reload();
          navigate('/');
        } else {
          // Handle error cases if needed
          console.error('Logout failed:', xhr.status, xhr.statusText);
        }
      }
    };
  };
  
  return (
    <div>
      <header>
      <Link style={{textDecoration:'none', color:'white', display:'flex'}} to="/">
      <img src={`${process.env.PUBLIC_URL}/images/logo.jpg`} alt="logo" style={{marginRight:'5px', borderRadius: '50%', width: '50px', height: '50px' }} />
        <h1 style={{ margin: 0 }}>&nbsp;  </h1>
        </Link>
        <div>
          {islogin ? (
            <div>
            <div style={{ display: 'flex' }}>
            <h3 style={{marginRight:'60px',  marginTop: '3px', fontWeight: 'bolder', color: 'white', textShadow: '2px 2px 2px black' }}>{userInfo.firstName}</h3>
            <FloatButton.Group style={{position: 'fixed', top: '15px', right: '20px' }}>
            <FloatButton className='button1' tooltip={<div>LogOut</div>} onClick={logOut} onMouseEnter={()=>{mouseEnter('button1')}} style={{color:'white', background:isEnter?'#E67E7A':'white'}} icon={<LogoutOutlined/>} size="large" />
            </FloatButton.Group>
            </div>
            <FloatButton
                  style={{zIndex:'9997', backgroundColor: isClicked ? '#B1D5DE' : 'white' }}
                  tyle="primary" onClick={showDrawer}
                  badge={{
                    count: numberOfItems,
                    color: 'blue',
                  }}
                  icon={<ShoppingCartOutlined />}
                />
                  <Drawer title="My Cart" onClose={onClose} open={open}>
                    <ul className="overflow">
                      {cartItems.map((item) => (
                        <li key={item._id} style={{ listStyleType: 'none' }}>
                          <CartItem item={item} userId={userId} />
                        </li>)
                      )}
                    </ul>
                    <div className='cartDescription'>
                      <h6  style={{ marginLeft: '200px' }}>{numberOfItems} item(s)</h6>
                      <h6 style={{ marginLeft: '200px' }}>total$: {
                        cartItems.reduce((acc, item) => acc + item.price, 0)}
                      </h6>
                      <div>
                      <button style={{marginTop:'0px'}} onClick={checkout} type="button" class="btn btn-outline-success btn-lg">Check Out</button>
                      </div>
                    </div>
                  </Drawer>
          </div>
          ) : (
            <>
              {/* <Link to="/signin">
              <button type="button" style={{background:"#519CF0"}} className="btn btn-primary">SignIn</button>
              </Link>
              <Link to="/signup">
              <button type="button" style={{background:'#9B3488'}} className="btn btn-success">SignUp</button>
              </Link> */}
              <>
              <>
              <FloatButton.Group style={{position: 'fixed', top: '15px', right: '20px' ,zIndex: '9999' }}>
                <FloatButton className='button1' onClick={handleButtonClick} onMouseEnter={()=>{mouseEnter('button1')}} style={{backgroundColor: isEnter?'#89DEF0':''}} icon={<UserOutlined />} size="large" />
                {showButtons && (
                  <>
                    <FloatButton  href="/signin" size="large" onMouseEnter={()=>{mouseEnter('button1')}} style={{textDecoration: "none",fontWeight:'bolder',backgroundColor: isEnter?'#89DEF0':''}} description="login" className='button2' />
                    <FloatButton href="/signup" size="large" onMouseEnter={()=>{mouseEnter('button1')}}  style={{textDecoration: "none",fontWeight:'bolder',backgroundColor: isEnter?'#89DEF0':''}} description="signup" className='button3' />
                  </>
                )}
              </FloatButton.Group>

              </>
              </>
            </>
          )}
        </div>
      </header>
    </div>
  );
}





