import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

export const fetchLoginStatus = createAsyncThunk('login/fetchLoginStatus', async () => {
    try {
        const response = await fetch('http://localhost:5500/member/islogin');
        if (!response.ok) {
            throw new Error('Failed to fetch login status');
        }
        return response.json();
    } catch (error) {
        throw new Error('Error fetching login status');
    }
});

const loginSlice = createSlice({
    name: 'login',
    initialState: {
        isLogin: false,
        userInfo: null,
        loading: false,
        error: null,
    },
    reducers: {
        removeFromCart: (state, action) => {
            const { userId, itemId } = action.payload;
            const user = state.userInfo;
            user.cart = user.cart.filter((id) => id !== itemId);
        },
        addCart: (state, action) => {
            //check if the item is already in the cart
            const { userId, itemId } = action.payload;
            const user = state.userInfo;
            if (!user.cart.includes(itemId)) {
                user.cart.push(itemId);
            }
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchLoginStatus.pending, (state) => {
                state.loading = true;
                state.error = null;
            })
            .addCase(fetchLoginStatus.fulfilled, (state, action) => {
                state.loading = false;
                if (action.payload.isLogin) {
                    state.isLogin = true;
                    state.userInfo = action.payload.userInfo;
                } else {
                    state.isLogin = false;
                    state.userInfo = null;
                }
            })
            .addCase(fetchLoginStatus.rejected, (state, action) => {
                state.loading = false;
                state.error = action.error.message;
            });
    },
});

export const { removeFromCart, addCart } = loginSlice.actions;
export default loginSlice.reducer;
