import React, { useState, useEffect } from 'react';
import { Button, Result } from 'antd';
import { useSelector } from 'react-redux';
import { nanoid } from '@reduxjs/toolkit';
import { useNavigate } from 'react-router-dom';
const ordernumber = "Order number: " + nanoid();
const Checkout = () => {
  const navigate = useNavigate();
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [region, setRegion] = useState('');
  const [country, setCountry] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [streetAddress, setStreetAddress] = useState('');
  const [creditCardNumber, setCreditCardNumber] = useState('');
  const [expiryDate, setExpiryDate] = useState('');
  const [cvv, setCvv] = useState('');
  const [submit, setSubmit] = useState(false);
  const isLogin = useSelector((state) => state.login.isLogin);
  const userInfo = useSelector((state) => state.login.userInfo);
const {_id} = userInfo;
  useEffect(() => {
    if (isLogin) {
      setFirstName(userInfo.firstName);
      setLastName(userInfo.lastName);
      setEmail(userInfo.email);
    };
  }, [isLogin, userInfo]);


  const submitCheckout = async (e) => {
    e.preventDefault();
    setSubmit(true);
  
    // Send a POST request to /removeAllFromCart
    const response = await fetch('http://localhost:5500/product/removeAllFromCart', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        userId: _id
      })
    });
  
    if (!response.ok) {
      console.error('Failed to remove all items from cart:', response.statusText);
    }
    
    setTimeout(() => {
      setSubmit(false);
      navigate('/');
      window.location.reload();
    }, 5000);
  };
  

  const goBack = () => {
    navigate('/');
    window.location.reload();
  };

  return (
    <div style={{ display: 'flex', marginTop:'30px',marginBottom:'90px', justifyContent: 'center' }}>
    {!submit?    <div>
      <div style={{ width: '50%' }}>
        <h2>Checkout</h2>
        <form >
          <div className="form-group">
            <label htmlFor="firstName">First Name:</label>
            <input
              type="text"
              className="form-control"
              id="firstName"
              value={firstName}
              onChange={(e) => setFirstName(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="lastName">Last Name:</label>
            <input
              type="text"
              className="form-control"
              id="lastName"
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="email">Email:</label>
            <input
              type="email"
              className="form-control"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="region">Region:</label>
            <input
              type="text"
              className="form-control"
              id="region"
              value={region}
              onChange={(e) => setRegion(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="country">Country:</label>
            <input
              type="text"
              className="form-control"
              id="country"
              value={country}
              onChange={(e) => setCountry(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="phoneNumber">Phone Number:</label>
            <input
              type="tel"
              className="form-control"
              id="phoneNumber"
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="streetAddress">Street Address:</label>
            <input
              type="text"
              className="form-control"
              id="streetAddress"
              value={streetAddress}
              onChange={(e) => setStreetAddress(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="creditCardNumber">Credit Card Number:</label>
            <input
              type="text"
              className="form-control"
              id="creditCardNumber"
              value={creditCardNumber}
              onChange={(e) => setCreditCardNumber(e.target.value)}
              required
            />
          </div>
          <div className="form-row">
            <div className="col">
              <label htmlFor="expiryDate">Expiry Date:</label>
              <input
                type="text"
                className="form-control"
                id="expiryDate"
                value={expiryDate}
                onChange={(e) => setExpiryDate(e.target.value)}
                required
              />
            </div>
            <div className="col">
              <label htmlFor="cvv">CVV:</label>
              <input
                type="text"
                className="form-control"
                id="cvv"
                value={cvv}
                onChange={(e) => setCvv(e.target.value)}
                required
              />
            </div>
          </div>
          <button  onClick={submitCheckout} type="submit" className="btn btn-primary mt-3">Submit</button>
        </form>
      </div>
      </div>:
      <Result
          status="success"
          title={ordernumber}
          subTitle="The shipment takes 1-5 business days. Please wait for confirmation."
          extra={[
            <Button onClick={goBack} key="continuePurchase">Continue Purchase</Button>,
          ]}
        />
      }

    </div>
  );
};

export default Checkout;
