import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Menu } from 'antd';
import { CoffeeOutlined, ShopOutlined, MehOutlined } from '@ant-design/icons';
import { SmileOutlined } from '@ant-design/icons';
import { useSelector } from 'react-redux';
import './main.css';
import Item from '../../Components/Item/Item';
import { Button, Result } from 'antd';

function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}

const category = [
  getItem('Men', 'sub1', <MehOutlined />, [
    getItem('Tops', '1'),
    getItem('Bottoms', '2'),
    getItem('Shoes', '3'),
  ]),
  getItem('Women', 'sub2', <CoffeeOutlined />, [
    getItem('Tops', '4'),
    getItem('Bottoms', '5'),
    getItem('Shoes', '6'),
  ]),
  getItem(
    <Link to="/others/*">
      Others
    </Link>,
    'link',
    <ShopOutlined />,
  ),
];

export default function Main() {
  const navigate = useNavigate();
  const { type, position } = useParams();
  const items = useSelector((state) => state.items.items);
  const [showMenu, setShowMenu] = useState(true);

  useEffect(() => {
    const handleResize = () => {
      setShowMenu(window.innerWidth >= 950);
    };

    // Add event listener to handle window resize
    window.addEventListener('resize', handleResize);

    // Initial check on component mount
    handleResize();

    // Remove event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  const goBack = () => {
    navigate(-1);
  };
  const handleMenTopsClick = () => {
    console.log('Men Tops clicked');
    navigate('/men/tops');
  };

  const handleMenBottomsClick = () => {
    console.log('Men Bottoms clicked');
    navigate('/men/bottoms');
  };

  const handleMenShoesClick = () => {
    console.log('Men Shoes clicked');
    navigate('/men/shoes')
  };

  const handleWomenTopsClick = () => {
    console.log('Women Tops clicked');
    navigate('/women/tops');
  };

  const handleWomenBottomsClick = () => {
    console.log('Women Bottoms clicked');
    navigate('/women/bottoms');
  };

  const handleWomenShoesClick = () => {
    console.log('Women Shoes clicked');
    navigate('/women/shoes');
  };


  const onClick = ({ key }) => {
    switch (key) {
      case '1':
        handleMenTopsClick();
        break;
      case '2':
        handleMenBottomsClick();
        break;
      case '3':
        handleMenShoesClick();
        break;
      case '4':
        handleWomenTopsClick();
        break;
      case '5':
        handleWomenBottomsClick();
        break;
      case '6':
        handleWomenShoesClick();
        break;
      default:
        console.log('Unknown category clicked');
        break;
    }
  };

  var newList = [];
  if (position === "*") {
    newList = items.filter(item => item.type === type);
  } else {
    newList = items.filter(item => item.type === type && item.position === position);
  }

  return (
    <div style={{ display: 'flex' }}>
      {showMenu && (
        <Menu
          onClick={onClick}
          style={{
            width: 256,
          }}
          defaultSelectedKeys={['1']}
          defaultOpenKeys={['sub4']}
          mode="inline"
          items={category}
        />
      )}
      <main className="main">
        {newList.length === 0? 
          <Result
            icon={<SmileOutlined />}
            title="Apologies, but we currently do not have any items available. Please check back later for updates."
            extra={<Button onClick={goBack} type="primary">Go Back</Button>}
          />
        :
        <ul>
          {newList.map(item => (
            <li key={item._id} className="item">
              <Item price={item.price} id={item._id} name={item.name} image={item.image} />
            </li>
          ))}
        </ul>}
      </main>
    </div>
  );
}
