import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Alert,Spin } from 'antd';
import './signup.css';

const SignUpForm = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
  });
  const [errorMessage, setErrorMessage] = useState('');
  const [isSignUp, setIsSignUp] = useState(false);

  const navigate = useNavigate();

  const validatePassword = (password) => {
    const regex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;
    return regex.test(password);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validatePassword(formData.password)) {
      setErrorMessage('Password must be at least 8 characters and include both letters and numbers');
      return;
    }

    try {
      const response = await fetch('http://localhost:5500/member/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        const contentType = response.headers.get('content-type');
        if (contentType && contentType.includes('application/json')) {
          const errorData = await response.json();
          console.error('Error:', errorData.error);
          setErrorMessage(errorData.error || 'An error occurred during signup. Please try again.');
        } else {
          console.error('Error:', response.statusText);
          setErrorMessage('An error occurred during signup. Please try again.');
        }
      } else {
        const responseData = await response.json();
        console.log('Success:', responseData);
        setIsSignUp(true);
        setTimeout(() => {
          setIsSignUp(false);
          navigate('/signin');
        }, 2000);
      }
    } catch (error) {
      console.error('Error:', error.message);
      setErrorMessage('An unexpected error occurred. Please try again.');
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    if (name === 'password') {
      setErrorMessage('');
    }
  };

  return (
    <div >
      {!isSignUp ? (     <div className="signup-form-container"> <h2>Sign Up</h2>
      <form onSubmit={handleSubmit}>
        <label htmlFor="firstName">First Name:</label>
        <input
          type="text"
          id="firstName"
          name="firstName"
          required
          value={formData.firstName}
          onChange={handleInputChange}
        />

        <label htmlFor="lastName">Last Name:</label>
        <input
          type="text"
          id="lastName"
          name="lastName"
          required
          value={formData.lastName}
          onChange={handleInputChange}
        />

        <label htmlFor="email">Email:</label>
        <input
          type="email"
          id="email"
          name="email"
          required
          value={formData.email}
          onChange={handleInputChange}
        />

        <label htmlFor="password">Password:</label>
        <input
          type="password"
          id="password"
          name="password"
          required
          value={formData.password}
          onChange={handleInputChange}
        />
        {errorMessage && <p>{errorMessage}</p>}
        <button type="submit">Sign Up</button>
      </form>
      </div>):  
              <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center',marginTop:'30px', justifyContent: 'center'}}>
          <Alert
            message="Congratulations!"
            description="You have successfully signed up!"
            type="success"
            showIcon
          /><div style={{marginTop:'30px'}}>
            <Spin size="large" />
          </div>

        </div>  
        }
    </div>
  );
};

export default SignUpForm;